<template>
    <!-- BEGIN: Content -->
    <div class="content">
        <h2 class="intro-y text-lg font-medium mt-10">
            Product Grid
        </h2>
        <div class="grid grid-cols-12 gap-6 mt-5">

            <!-- BEGIN: Users Layout -->
            <div class="intro-y col-span-12 md:col-span-6 lg:col-span-4 xl:col-span-3">
                <div class="box">
                    <div class="p-5">
                        <div
                            class="h-40 2xl:h-56 image-fit rounded-md overflow-hidden before:block before:absolute before:w-full before:h-full before:top-0 before:left-0 before:z-10 before:bg-gradient-to-t before:from-black before:to-black/10">
                            <img alt="Midone - HTML Admin Template" class="rounded-md" :src="url + form.image">
                        </div>
                        <div class="text-slate-600 dark:text-slate-500 mt-5">
                            <div class="flex items-center"> <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24"
                                    viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2"
                                    stroke-linecap="round" stroke-linejoin="round" icon-name="link" data-lucide="link"
                                    class="lucide lucide-link w-4 h-4 mr-2">
                                    <path d="M10 13a5 5 0 007.54.54l3-3a5 5 0 00-7.07-7.07l-1.72 1.71"></path>
                                    <path d="M14 11a5 5 0 00-7.54-.54l-3 3a5 5 0 007.07 7.07l1.71-1.71"></path>
                                </svg> Title: {{ form.title }}
                            </div>
                            <div class="flex items-center  mt-2"> <i data-lucide="link" class="w-4 h-4 mr-2"></i> Subtitle:
                                {{
                                    form.subtitle }}
                            </div>
                            <div class="flex items-center  mt-2"> <i data-lucide="link" class="w-4 h-4 mr-2"></i> Price: {{
                               "$"+form.price }}
                            </div>
                            <div class="flex items-center mt-2"> <i data-lucide="layers" class="w-4 h-4 mr-2"></i>Rating: {{
                                form.rating }} </div>
                            <div class="flex items-center mt-2"> <i data-lucide="check-square" class="w-4 h-4 mr-2"></i>
                                Reviews: {{ form.reviews }} </div>
                        </div>
                    </div>
                    <div
                        class="flex justify-center lg:justify-end items-center p-5 border-t border-slate-200/60 dark:border-darkmode-400">
                        <a class="flex items-center text-primary mr-auto" href="javascript:;"><svg
                                xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none"
                                stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"
                                icon-name="eye" data-lucide="eye" class="lucide lucide-eye w-4 h-4 mr-1">
                                <path d="M1 12s4-8 11-8 11 8 11 8-4 8-11 8-11-8-11-8z"></path>
                                <circle cx="12" cy="12" r="3"></circle>
                            </svg> Preview </a>
                            <router-link :to="{ path: '/admin/edit-services/' + this.userId }">
                        <a class="flex items-center mr-3" href="javascript:;"> <svg xmlns="http://www.w3.org/2000/svg"
                                width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor"
                                stroke-width="2" stroke-linecap="round" stroke-linejoin="round" icon-name="check-square"
                                data-lucide="check-square" class="lucide lucide-check-square w-4 h-4 mr-1">
                                <polyline points="9 11 12 14 22 4"></polyline>
                                <path d="M21 12v7a2 2 0 01-2 2H5a2 2 0 01-2-2V5a2 2 0 012-2h11"></path>
                            </svg>Edit </a></router-link>
                        <a class="flex items-center text-danger" href="javascript:;" data-tw-toggle="modal"
                        v-bind:data-tw-target="'#delete-confirmation-modal-' + this.userId"><svg xmlns="http://www.w3.org/2000/svg" width="24"
                                height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2"
                                stroke-linecap="round" stroke-linejoin="round" icon-name="trash-2" data-lucide="trash-2"
                                class="lucide lucide-trash-2 w-4 h-4 mr-1">
                                <polyline points="3 6 5 6 21 6"></polyline>
                                <path d="M19 6v14a2 2 0 01-2 2H7a2 2 0 01-2-2V6m3 0V4a2 2 0 012-2h4a2 2 0 012 2v2"></path>
                                <line x1="10" y1="11" x2="10" y2="17"></line>
                                <line x1="14" y1="11" x2="14" y2="17"></line>
                            </svg>
                            Delete </a>
                    </div>
                </div>
                  <!-- BEGIN: Delete Confirmation Modal -->
                  <div :id="'delete-confirmation-modal-' + this.userId" class="modal" tabindex="-1" aria-hidden="true">
                    <div class="modal-dialog">
                        <div class="modal-content">
                            <div class="modal-body p-0">
                                <div class="p-5 text-center">
                                    <i data-lucide="x-circle" class="w-16 h-16 text-danger mx-auto mt-3"></i> 
                                    <div class="text-3xl mt-5">Are you sure?</div>
                                    <div class="text-slate-500 mt-2">
                                        Do you really want to delete these records? 
                                        <br>
                                        This process cannot be undone.
                                    </div>
                                </div>
                                <div class="px-5 pb-8 text-center">
                                    <button type="button" data-tw-dismiss="modal" class="btn btn-outline-secondary w-24 mr-1">Cancel</button>
                                    <button type="button" class="btn btn-danger w-24"  v-on:click="deleteServices">Delete</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <!-- END: Delete Confirmation Modal -->
            </div>
            <!-- END: Users Layout -->
            <span v-if="error">{{ error }}</span>
        </div>
    </div>
    <!-- END: Content -->
</template>
<script>
import { deleteRequest, getRequestById } from '../ApiFunctions'

export default {
    name: 'ViewServices',
    data() {
        return {
            userId:'',
            url: 'https://workshires.com:9000/api/ServiceImage/',
            form: {
                title: '',
                subtitle: '',
                rating: null,
                reviews: null,
                price: null,
                image: null,
            },
            error:''
        }

    },
    mounted() {
        this.userId = this.$route.params.id
        this.viewData(this.$route.params.id)
    },
    methods: {
        async viewData(id) {
            const res = await getRequestById(`/get-service/id/${id}`)
            this.form.price = res.data.price
            this.form.image = res.data.profile
            this.form.reviews = res.data.reviews
            this.form.rating = res.data.rating
            this.form.title = res.data.title
            this.form.subtitle = res.data.subtitle
        },
        async deleteServices () {
         try{
            await deleteRequest(`/remove-service/${this.userId}`)
            this.$router.push({ name: 'ServicesTable', })
            const modalId = `#delete-confirmation-modal-${this.userId}`;
            const modal = document.querySelector(modalId);
            if (modal) {
                modal.classList.remove('show');
                modal.setAttribute('aria-hidden', true);
                modal.removeAttribute('style');
                const modalBackdrop = document.querySelector('.modal-backdrop');
                if (modalBackdrop) {
                    modalBackdrop.parentNode.removeChild(modalBackdrop);
                }
            }
        

         }
         catch(err){
            this.error = err
         }
        }
    }
}
</script>



